<template>
    <div class="space-around login-form">
        <button @click="loginWithMicrosoft" class="btn_submit btn_md">Login with Microsoft</button>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                email: '',
                password: '',
            }
        },
        methods: {
            loginWithMicrosoft() {
                this.$hub.emit('redirect to microsoft login');
            }
        }
    }
</script>
